import { Item } from './item';
import { BiddingData } from './biddingData';
import { PicInfo } from './picInfo';
import { CorrdataInfo } from './corrdataInfo';
export class Catalogue {
  commission?: number;//佣金比例
  LOTSTATUS?: string;//拍品状态{"0001":"正常,"0002":"结束"}
  ENDTIME?: number;//下线时间 （此为长整型，需要自己转换为时间）
  STARTTIME?: number;//开拍时间 （此为长整型，需要自己转换为时间）
  ITEMNAME?: string;//拍品名称
  ITEMID: number;//拍品编号
  LOTID: number;//拍品lot编号（很重要）
  GLIDEMARK: number;//拍品流水号
  INCRMODE?: string;//竞价阶梯方式（"0001":"增量阶梯","0002""固定阶梯"）
  INCRPRICE?: number;//阶梯价格
  FIRSTCATGID?: number;//一级分类ID
  FIRSTCATGNAME?: string;//一级分类名称
  SECONDCATGID?: number;//二级分类ID
  SECONDCATGNAME?: string;//二级分类名称
  BIDMODE?: string;//竞价方式 "0001":"一口价","0002":"速胜","0003":"英式"
  BIDCOUNT?: number;//竞价次数
  STARTPRICE?: number;//起拍价格
  RSRVPRICE?: number;//拍品保留价
  CURRENTPRICE?: number;//当前价格(无出价时为起拍价,出价后为最新出价)
  proxyFlag?: string;//此拍品是否有代理出价 "Y":有,"N":无 如果有的话下边有一个属性（itemProxyInfo）为代理的具体信息
  proxyTime?: string;//代理出价 出价时间 只有“proxyFlag”为 “Y”时才有此字段
  itemInfo?: Item;
  currentBid?: BiddingData;
  bids?: BiddingData[];
  majorItemPic?: PicInfo;
  otherItemPicList?: PicInfo[];
  corrdataList?: CorrdataInfo[];
}



