export class BiddingData {
  id: number;//出价记录表中的id号
  isLast?: string;//是否最新出价{"Y":"是","N":"否"}
  bidPrice: number;//出价
  isDisplay?: string;//是否显示{"Y":"显示","N":"不显示"}
  clinetIp?: string;//客户端IP
  isAbsentee?: string;//此次出价是否代理{"Y":"是","N":"否"}
  lotId: number;//拍品lot编号
  bidderId: number;//竞买人ID
  bidderName: string;//竞买人名称
  bidNumber: number;//第几次出价
  referer?: string;//客户端来源REFFER
  bidTime: number;//精确出价时间 （此为长整型，需要自己转换为时间）
  depositCoefficient?: number;//保证金系数
  endTime?: number;//拍品拍卖结束时间，即图录表中的结束时间字段
  delFlag?: string;//逻辑删除{"Y":"是","N":"否"}
  addBy?: string;//记录创建人
  addTime?: number;//记录创建日期（此为长整型，需要自己转换为时间）
  modBy?: string;//记录修改人
  modTime?: string;//记录修改日期（此为长整型，需要自己转换为时间）
}



