import { PicDetail } from './modals/picdetail/picdetail';
import { TimeDiffer } from '../../pipes/time-differ';
import { Bidmode } from '../../pipes/bidmode';

@NgModule({
  declarations: [
    ItemPage,
    TimeDiffer,
    Bidmode,
    PicDetail
  ],
  imports: [
    IonicPageModule.forChild(ItemPage),
  ],
  exports: [
    ItemPage
  ]
})
export class ItemPageModule { }



