import { Pipe, PipeTransform } from '@angular/core';

/**
 * Generated class for the Time pipe.
 *
 * See https://angular.io/docs/ts/latest/guide/pipes.html for more info on
 * Angular Pipes.
 */
@Pipe({
  name: 'timeDiffer',
})
export class TimeDiffer implements PipeTransform {
  /**
   * transform the number type time to days, hours,minutes and seconds
   */
  transform(value: number, ...args) {
    let days = Math.floor(value / 1000 / 60 / 60 / 24);
    let hours = Math.floor(value / 1000 / 60 / 60 % 24);
    let minutes = Math.floor(value / 1000 / 60 % 60);
    let seconds = Math.floor(value / 1000 % 60);
    return days + '天 ' + hours + '小时 ' + minutes + '分钟 ' + seconds + '秒';
  }
}



