export class Item {
  itemId: number;//拍品编号
  glideMark: string;//拍品流水号
  itemName: string;//拍品名称
  authorId?: string;//作者ID
  authorName?: string;//作者姓名
  itemAge?: string;//拍品年代
  firstCatgId?: string;//一级分类ID
  length?: number;//长
  width?: number;//宽
  height?: number;//高
  diameter?: number;//直径
  weight?: number;//拍品重量
  material?: string;//拍品质地
  style?: string;//拍品形式
  itemDesc?: string;//拍品描述
  marketPrice?: number;//市场估价
  itemStatus?: string;//拍品状态{'0001':正常，'0000':结束 }
  catgId?: number;//拍品分类ID
  parentId?: number;//catgId的父分类ID
  delFlag?: string;//逻辑删除{"Y":"是", "N":"否" }
  itemPicPath?: string;//图片路径（好像暂时不用这个路径）
  addBy?: string;//记录创建人
  addTime?: number;//记录创建日期（此为长整型，需要自己转换为时间）
  modBy?: string;//记录修改人
  modTime?: number;//记录修改日期（此为长整型，需要自己转换为时间）
}



