import {
  Component, ViewChild
} from '@angular/core';
import { IonicPage, NavController, NavParams, Content, Slides, AlertController, ToastController, ModalController, LoadingController } from 'ionic-angular';
import { Item } from '../../models/item';
import { BiddingData } from '../../models/biddingData';
import { User } from '../../models/user';
import { ItemService } from '../../service/item';
import { CatalogueService } from '../../service/catalogue';
import { Catalogue } from '../../models/catalogue';
import { Subscription } from 'rxjs/Subscription';
import { Storages } from '../../providers/storages';

@IonicPage()
@Component({
  selector: 'page-item',
  templateUrl: 'item.html',
  providers: [ItemService, Storages, CatalogueService]
})
export class ItemPage {

  @ViewChild(Content) content: Content;
  @ViewChild(Slides) slides: Slides;

  bidingsListenSubscription: Subscription;

  item: Item;
  catalogue: Catalogue;
  bidPrice: number;//待出价
  detail: string;
  startTimeCountDown: number;
  endTimeCountDown: number;
  pageStatus: string;
  user: User;

  cmmssnValue: number = 0;//当前佣金
  chargeprice: number = 0;

  constructor(public navCtrl: NavController, public navParams: NavParams, public altCtrl: AlertController, public toaCtrl: ToastController, public modCtrl: ModalController, private itemService: ItemService, private catalogueService: CatalogueService, private storages: Storages, private loadingCtrl: LoadingController) {
    this.detail = 'description';
    this.item = new Item();
    this.catalogue = new Catalogue();
    this.user = this.storages.get('User') || new User();
  }

  private caculateCharge(bidPrice, commission) {
    this.cmmssnValue = Math.round(bidPrice / commission);
    this.chargeprice = this.cmmssnValue + Number(bidPrice);
  }

  ngAfterViewInit() {
    let loading = this.loadingCtrl.create();
    loading.present();

    let item = this.navParams.get('item');
    let lotId = item.LOT_ID;
    let saleRoomId = this.navParams.get('saleRoomId');
    this.catalogueService.getCatalogueInfo(lotId, saleRoomId, this.user.memberId).subscribe(catalogue => {
      this.catalogue = catalogue;

      if (this.catalogue.BIDCOUNT > 0) {
        this.bidPrice = this.catalogue.CURRENTPRICE;
        this.addPrice();
      } else {
        this.bidPrice = this.catalogue.STARTPRICE;

      }

      let countDownInterval = setInterval(() => {

        this.startTimeCountDown = catalogue.STARTTIME - new Date().getTime();
        this.endTimeCountDown = catalogue.ENDTIME - new Date().getTime();

        if (this.startTimeCountDown > 0) {
          this.pageStatus = 'noAction';
        } else if (this.endTimeCountDown > 0) {
          this.pageStatus = 'actioning';
        } else {
          this.pageStatus = 'actioned';
        }
      }, 1000);

      if (this.catalogue.LOTSTATUS === '0008') {
        clearInterval(countDownInterval);
      }

      this.bidingsListenSubscription = this.catalogueService.listenBidings(this.catalogue.LOTID).subscribe(message => {
        //解析非标准json
        let data = (new Function('return ' + message.data))();

        let newbid = new BiddingData();
        newbid = data.bids[0];
        this.catalogue.CURRENTPRICE = newbid.bidPrice;
        this.catalogue.currentBid = newbid;
        this.bidPrice = data.nextPrice;

        this.catalogue.bids.unshift(newbid);
      });

      loading.dismiss();
    });

  }

  ngOnDestroy() {
    this.bidingsListenSubscription.unsubscribe();
    this.itemService.disListenBidings();
  }


  picDetail() {
    this.modCtrl.create('PicDetail', {
      pics: this.catalogue.otherItemPicList
    }).present();
  }

  addPrice() {
    if (!this.catalogue.INCRPRICE) {
      this.catalogueService.getNextPrice(this.catalogue.LOTID, this.bidPrice, this.user.memberId).subscribe(price => {
        this.bidPrice = price;
      });
    } else {
      this.bidPrice = this.bidPrice + this.catalogue.INCRPRICE;
    }
  }

  reducePrice() {
    if (!this.catalogue.INCRPRICE) {
      this.catalogueService.getPrevPrice(this.catalogue.LOTID, this.bidPrice, this.user.memberId).subscribe(price => {
        if (price > 0) {
          this.bidPrice = price;
        } else {
          this.toaCtrl.create({
            message: '出价不能低于当前价!',
            duration: 1000,
            dismissOnPageChange: true
          }).present();
        }
      });
    } else {
      if (this.bidPrice > this.catalogue.CURRENTPRICE) {
        this.bidPrice -= this.catalogue.INCRPRICE;
      } else {
        this.toaCtrl.create({
          message: '出价不能低于当前价!',
          duration: 1000,
          //showCloseButton: true,
          closeButtonText: '好的',
          dismissOnPageChange: true
        }).present();
      }

    }
  }



  giveNormalPrice() {
    if (!this.storages.get('User')) {
      return this.navCtrl.push('LoginPage');
    }
    this.caculateCharge(this.bidPrice, this.catalogue.commission);
    let title = '';
    if (this.catalogue.currentBid && this.catalogue.currentBid.bidderId === Number(this.user.memberId)) {
      title = '您已领先 是否再次出价？';
    } else {
      title = '确认出价？';
    }
    let confirm = this.altCtrl.create({
      title: title,
      message: "出价￥" + this.bidPrice + '<br>含佣金价￥' + this.chargeprice,
      buttons: [
        {
          text: '取消',
          handler: data => {
            //console.log('Cancel clicked');
          }
        },
        {
          text: '确定',
          handler: data => {
            this.catalogueService.giveNormalPrice(this.catalogue.LOTID, this.user.memberId, this.bidPrice).subscribe(result => {
              this.toaCtrl.create({
                message: result,
                duration: 1000,
                dismissOnPageChange: true
              }).present();
            });
          }
        }
      ]
    });
    confirm.present();
  }

  giveProxyPrice() {
    if (!this.storages.get('User')) {
      return this.navCtrl.push('LoginPage');
    }
    this.caculateCharge(this.bidPrice, this.catalogue.commission);
    let title;
    if (this.catalogue.currentBid && this.catalogue.currentBid.bidderId === Number(this.user.memberId)) {
      title = '您已领先 是否再次出价？';
    } else {
      title = '确认出价？';
    }
    let confirm = this.altCtrl.create({
      title: '确认代理出价？',
      message: "出价￥" + this.bidPrice + '<br>含佣金价￥' + this.chargeprice,
      buttons: [
        {
          text: '取消',
          handler: data => {
            //console.log('Cancel clicked');
          }
        },
        {
          text: '确定',
          handler: data => {
            this.catalogueService.giveProxyPrice(this.catalogue.LOTID, this.user.memberId, this.bidPrice).subscribe(result => {
              this.toaCtrl.create({
                message: result,
                duration: 1000,
                dismissOnPageChange: true
              }).present();
            });
          }
        }
      ]
    });
    confirm.present();
  }

  collectItem() {
    // if (this.item.isCared == 'Y') {
    //   this.itemService.caredItemMod('uncare', this.item.itemnum).subscribe(result => {
    //     if (result) {
    //       this.item.isCared = 'N';
    //     }
    //   })
    // } else {
    //   this.itemService.caredItemMod('care', this.item.itemnum).subscribe(result => {
    //     if (result) {
    //       this.item.isCared = 'Y';
    //     }
    //   })
    // }
  }

  setAlert() {

  }

  shareItem() {

  }





}



